.DialogMessage_cloud__1j_yI{
    min-height: 73px;
    background-color: #2f7e7e;
    border-radius: 25px 25px 25px 0;
    padding: 10px;
    position:relative;
}

.DialogMessage_cloud__1j_yI:before{
    content: '';
    position: absolute;
    border: 10px solid transparent; border-right: 10px solid #2f7e7e; border-bottom: 10px solid #2f7e7e;
    left: -20px;
    top: 53px;
}

.DialogMessage_cloud__1j_yI:after{
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius:50%;
    background: aquamarine;
    left: -30px;
    top: 43px;
}


.DialogMessage_cloudAfter__OAWHp{
    min-height: 73px;
    background-color: #2F8063FF;
    border-radius: 25px 0 25px 25px;
    padding: 10px;
    position:relative;
}

.DialogMessage_cloudAfter__OAWHp:before{
    content: '';
    position: absolute;
    border: 10px solid transparent; border-left: 10px solid #2F8063FF; border-top: 10px solid #2F8063FF;
    right: -20px;
    top: 0;
}

.DialogMessage_cloudAfter__OAWHp:after{
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius:50%;
    background: aquamarine;
    right: -30px;
    top: 0;
}

.DialogMessage_round__1AOFv{
    padding-bottom: 2px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 35px;
    min-height: 35px;
    border-radius: 50%;
    background-color:  #E44D3A;

}

.DialogMessage_roundFirst__1ePsc{
    align-self: start;
    margin-right: 30px;
}

.DialogMessage_roundSecond__17Lon{

    align-self: start;
    margin-left: 30px;
}
